input[type=range]:focus::-webkit-slider-runnable-track {
    background: #000000;
}
input[type=range]::-moz-range-track {
    width: 100%;
    height: 8.4px;
    cursor: pointer;
    box-shadow: 1px 1px 1px #000000, 0 0 1px #0d0d0d;
    background: #3071a9;
    border-radius: 1.3px;
    border: 0.2px solid #010101;
}
input[type=range]::-moz-range-thumb {
    box-shadow: 1px 1px 1px #000000, 0 0 1px #0d0d0d;
    border: 1px solid #000000;
    height: 36px;
    width: 16px;
    border-radius: 3px;
    background: #ffffff;
    cursor: pointer;
}
input[type=range]::-ms-track {
    width: 100%;
    height: 8.4px;
    cursor: pointer;
    background: transparent;
    border-color: transparent;
    border-width: 16px 0;
    color: transparent;
}
input[type=range]::-ms-fill-lower {
    background: #2a6495;
    border: 0.2px solid #010101;
    border-radius: 2.6px;
    box-shadow: 1px 1px 1px #000000, 0 0 1px #0d0d0d;
}
input[type=range]::-ms-fill-upper {
    background: #3071a9;
    border: 0.2px solid #010101;
    border-radius: 2.6px;
    box-shadow: 1px 1px 1px #000000, 0 0 1px #0d0d0d;
}
input[type=range]::-ms-thumb {
    box-shadow: 1px 1px 1px #000000, 0 0 1px #0d0d0d;
    border: 1px solid #000000;
    height: 36px;
    width: 16px;
    border-radius: 3px;
    background: #ffffff;
    cursor: pointer;
}

body {
    text-align: center;
}

#slider-form {
    margin-top: 5%;
    margin-right: 5%;
    text-align: right;
}

.playPauseButton {
    border: none;
    background: transparent;
}

.playPauseButton:active {
    padding: 3px 0 0 3px;
    background: transparent;
}

.playPauseIcon {
    width: 100px;
    height: 100px;
    fill: black;
}

.loading {
    width: 100%;
    line-height: 100px;
    text-align: center;
    font-size: x-large;
    font-weight: bold;
}

.bottomDiv {
    float: bottom;
    width: 100%;
    margin-top: 25px;
}

#time-slider-and-output-div {
    height: 50px;
}

.bpm {
    padding-right: 15px;
}

.sliderTable {
    padding-top: 2vh;
    width: 100vw;
}

.tempoCell {
    padding-top: 30px;
}

.sliderCell {
    text-align: left;
    /*
     * causes Android browser to fire onPointerUp event instead of eating it
     * https://github.com/facebook/react/issues/13333
     */
    touch-action: none;
}

.sliderLabel {
    font-weight: bold;
    font-size: larger;
}

.sliderLabelColumn {
    width: 25vw;
}

