.churches {
  display: flex;
  flex-flow: column;
  text-align: left;
  padding: 15px;
  padding-bottom: 180px;
}

.church {
  padding-top: 15px;
}

.churchName {
  line-height: 25px;
  padding-left: 6px;
  text-align: center;
  font-size: large;
  vertical-align: middle;
}

.selectedChurch {
  font-weight: bold;
}

.churchRadioButton {
  width: 25px;
  height: 25px;
  vertical-align: middle;
}
