.footer {
    display: flex;
    width: 100vw;
    justify-content: space-between;
    border-top: 2px solid;
    position: fixed;
    bottom: 0;
    background-color: white;
    padding-bottom: 10px;

}

.calendarIcon {
    width: 50px;
    height: 50px;
    padding: 0 15px 0 25px;
}

.libraryBooksIcon {
    width: 40px;
    height: 40px;
    padding: 5px 15px 0 15px;
}

.settingsIcon {
    width: 45px;
    height: 45px;
    padding: 3px 15px 0 15px;
}

.helpIcon {
    width: 45px;
    height: 45px;
    padding: 2px 25px 0 15px;
}

.controlsIcon {
    width: 52px;
    height: 52px;
    padding: 0 35px 0 35px;
}

.pdfIcon {
    width: 47px;
    height: 47px;
    padding: 1px 35px 0 35px;
}

.infoIcon {
    width: 50px;
    height: 50px;
    padding: 1px 35px 0 35px;
}

.link {
    color: black;
}