.header {
  display: flex;
  height: 10vh;
  width: 100vw;
  align-items: center;
  border-bottom: 2px solid;
  white-space: nowrap;
}

.title {
  align-items: center;
  justify-content: center;
  font-size: 14pt;
  font-weight: bold;
  text-overflow: ellipsis;
  white-space: normal;
  width: 100%;
}

.backButton {
  align-items: center;
  width: 30px;
  height: 100%;
  padding-left: 5px;
  padding-right: 10px;
}

.rightSpan {
  width: 30px;
  padding-left: 5px;
  padding-right: 10px;
}

#back-button-link {
  text-decoration: none;
  color: black;
}