.App {
  /*text-align: center;*/
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

#hymnal-header, #songs-header{
  text-align: center;
}

#hymn-list, #hymnal-list {
  text-align: left;
  text-underline: none;
}

#this-week-troubleshooting-link {
  text-decoration: #723ee7 !important;
}

#hymn-list a, #hymnal-div a, #homepage-list a, #hymnal-list a, #churches-list a, #menu-list a, u {
  text-decoration: none;
  color: black;
}

ul {
  list-style-type: none;
  padding-top: 0;
  padding-left: 5%;
}

#hymn-list a:visited, #hymnal-div a:visited #homepage-list a:visited, #hymnal-list a:visited,
#churches-list a:visited, #menu-list a:visited {
  text-decoration: none;
  color: black;
}

#homepage-list li {
  padding: 4px;
  white-space: nowrap;
}

#hymn-list li, #hymnal-list li, #churches-list li, #menu-list li {
  padding: 4px;
  white-space: nowrap;
  text-align: left;
}

.this-week-table {
  padding: 4px;
  border: 1px black;
  text-align: left;
}

#hymnal-div {
  margin-top: 10px;
}

#this-week-error-troubleshooting, #hymnal-error-troubleshooting {
  text-align: left;
  margin: 10px;
}

#this-week-error-troubleshooting ul li, #hymnal-error-troubleshooting ul li {
  margin: 3px;
}

#this-week-error-troubleshooting ul, #hymnal-error-troubleshooting ul {
  list-style-type: '- ' !important;
}

#church-selector-label {
  margin: 5px;
}

.outerPageContent {
  display: flex;
  flex-flow: column;
  height: 100vh;
}

.innerPageContent {
  height: 100vh;
  overflow-y: auto;
  overflow-x: auto;
  white-space: nowrap;
}

.songList {
  padding-bottom: 170px;
}

.searchDiv {
  margin-top: 15px;
  width: 100vw;
  align-content: center;
  position: relative;
}

.searchInput {
  width: 85%;
  padding: 10px;
  font-size: medium;
}

.searchInput:focus {
  outline: 0 none;
}

.cancelSearch {
  top: 8px;
  right: 7%;
  position: absolute;
  z-index:1000;
}

.cancelSearchIcon {
  width: 30px;
  height: 30px;
}